export const Task = connect(state=>({profile:state.profile}))((props) => {
    const [newBieTasks, setNewBieTasks] = useState({list:[]});
    const [dailyTasks, setDailyTasks] = useState({list:[]});

    useEffect(() => {
        console.log('[debug]profile:',props.profile);
        getNewbieTaskList().then(ret => {
            setNewBieTasks(
                {
                    label:'Newbie Tasks',
                    list:ret.data
            });
        });
        if(props.profile)
        {
            getDailyTaskList(props.profile?.code).then(ret => {
                setDailyTasks({
                    label:'Daily Tasks',
                    list:ret.data
                });
            });
        }
        
    }, [props.profile])
    return (
        <div className='quest'>
            <div className='quest-header'>
                <QuestHeader/>
            </div>
            <div className="quest-content">
                <div className="full-r-w full-r-h quest-content-cxt">
                    <img className="quest-content-bg" mode="widthFix" src={SplitBgImg} />
                    <div className="quest-content-container">

                    </div>
                    <div className="full-r-w quest-list-container">
                        <TaskList list1={newBieTasks} list2={dailyTasks}/>
                    </div>
                </div>
            </div>
        </div>
    )
})