import { Box, CircularProgress } from "@mui/material";
import { useEffect } from "react";
import { useNavigate, useParams, useSearchParams } from "react-router-dom";
import { discordCallback, socialMediaCallBack } from "src/api";

export const CallbackDiscord = (props) => {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const { type } = useParams();
  useEffect(() => {
    if (type === "discord") {
      const state = searchParams.get("state") || "";
      const error = searchParams.get("error") || "";
      const code = searchParams.get("code") || "";
      discordCallback(code, state, error).then((res) => {
        if (res.code === 200) {
          navigate("/app/quest");
        }
      });
    } else if (type === "twitter") {
      const oauth_token = searchParams.get("oauth_token") || "";
      const oauth_verifier = searchParams.get("oauth_verifier") || "";

      if (oauth_token && oauth_verifier) {
        socialMediaCallBack("twitter", oauth_token, oauth_verifier)
          .then((ret) => {
            if (ret.code === 200) {
              navigate("/app/quest");
            }
          })
          .catch((err) => {
            console.error(err);
          });
      }
    }
  }, [navigate, searchParams, type]);
  return (
    <Box
      sx={{
        height: "100%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <Box
        sx={{
          mt: 12,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <Box>
          <CircularProgress />
        </Box>
        <Box>...</Box>
      </Box>
    </Box>
  );
};
