
import rightArrowImg from 'src/assets/images/right-arrow.svg';
import "./mainnav.css";
import { Link } from 'react-router-dom';

import { Box, Grid } from '@mui/material';

const NavElement = ({ img, title, link }) => {
    return (
        <Grid item className="main-nav" xs={12} md={6} lg={4} xl={3}>
            <img src={img} className='nav-image'></img>
            <Box className="block-cover" sx={{ ml: 1 }}>
                <div className='block-bar'>
                    <div className='nav-title'>
                        {title}
                    </div>
                    <Link className="block-button" onClick={() => {
                        window.scrollTo(0, 0)
                    }} to={link}>
                        <img src={rightArrowImg} />
                    </Link>
                </div>

            </Box>
        </Grid>
    )
}
export const MainNav = ({ navs }) => {
    return (
        <div style={{ backgroundColor: 'black', display: 'none' }}>
            <div className='main-nav-container'>
                {/* <div className="full-r-w " style={{position:'absolute',left:0,right:0}}>
              <img src={HomeFooterBg} style={{width:'100%'}}/>  
            </div> */}

                <div className='main-nav-content' style={{ width: 1600, flexWrap: 'wrap', justifyContent: 'flex-start' }}>
                    <Grid container spacing={1}>
                        {
                            navs.map((nav, idx) => {
                                return <NavElement key={'nek_' + idx} img={nav.img} title={nav.title} link={nav.link} />
                            })
                        }
                    </Grid>
                </div>

            </div>
        </div>
    )
}