import { useState,useRef,useEffect } from "react";
import { WelcomeVideo,WelcomePoster } from "src/assets";
import { useNavigate } from 'react-router-dom';

import "./welcome.css";
export const Welcome=(props)=>{
    const navigate = useNavigate();
    let [autoPlay,setAutoPlay]=useState(true);
    const videoRef = useRef();
    useEffect(()=>{
        setTimeout(()=>{
            navigate('/app');
        },5000)
    },[])
    return (
        <div className="full-r-w gameplay" onClick={()=>{
            navigate('/app');
        }}>
            <video ref={videoRef}   className="welcome-video" 
            muted={true}
            autoPlay={autoPlay} src={WelcomeVideo}>

            </video>
        </div>
    )
}