import { SMArrowImg } from "src/assets";
import "./task-list.css";
import { QuestListItem } from "src/components/task/quest-list-item";
import { OnChainItem } from "src/components/task/on-chain-item";
import { NewbieTask } from "src/components/task/newbie-tasks";

// const QuestDialogs = {
//     FOLLOW_TWITTER_QUEST:
// }

export const TaskList = ({ list1 = { list: [] }, list2 = { list: [] } }) => {
  return (
    <div className="quest-list quest-contain fx-box fx-fd-c fx-ai-c">
      <div className="quest-list-1">
        <div className="quest-list-label">
          <span className="quest-list-label-pt"></span>
          <span className="quest-list-label-text">{list1?.label}</span>
        </div>
        {list1?.list && (
          <div className="quest-list-box">
            {list1?.list?.map((item, idx) => {
              return (
                <NewbieTask subTask={item} key={"l1k_" + idx}></NewbieTask>
              );
            })}
          </div>
        )}
      </div>

      {list2?.list?.some((el) => !el.chainTask) && (
        <div className="quest-list-2">
          <div className="quest-list-label">
            <span className="quest-list-label-pt"></span>
            <span className="quest-list-label-text">{list2?.label}</span>
          </div>

          <div className="quest-list-box">
            {list2?.list
              ?.filter((el) => !el.chainTask)
              .map((item, idx) => {
                return (
                  <>
                    <div className="quest-list-item quest-2-h fx-box fx-jc-sb fx-ai-c">
                      <div className="quest-list-left">{item?.title}</div>
                      <div className="quest-list-right">
                        <img className="quest-2-img" alt="" src={SMArrowImg} />
                      </div>
                    </div>
                    <div className="quest-drop-menu">
                      {item.tasksRsps.map((subTask, sub_idx) => {
                        return (
                          <QuestListItem
                            subTask={subTask}
                            key={"sub_l2k_" + sub_idx}
                          ></QuestListItem>
                        );
                      })}
                    </div>
                  </>
                );
              })}

            {list2?.list
              ?.filter((el) => el.chainTask)
              .map((item, idx) => {
                return (
                  item.tasksRsps &&
                  item.tasksRsps.map((subTask) => (
                    <OnChainItem
                      key={`${subTask.id}-${subTask.title}`}
                      subTask={subTask}
                    ></OnChainItem>
                  ))
                );
              })}
          </div>
        </div>
      )}

      {/* <FollowTaskDialog visible={showFTDlg} quest={quest} onClose={()=>{setShowFTDlg(false)}}/> */}
    </div>
  );
};
