import "./players.css";
import React, { useState, useEffect, useRef } from "react";

import AudioControls from "./AudioControls";
import Slider from "./slider";

let counter = 1;
let audioLong = 0;
// let inter = null;
// let playIndex = 0;
export const AudioControl = (props) => {
  const { audioList } = props;
  const { autoplay } = props;
  const [trackIndex, setTrackIndex] = useState(0);
  const [trackProgress, setTrackProgress] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);
  const [text, setText] = useState("00:00");

  let current = audioList[trackIndex];

  // Refs
  const audioRef = useRef(new Audio(current.link));
  const intervalRef = useRef();
  const isReady = useRef(false);

  const { duration } = audioRef.current;

  const currentPercentage = duration
    ? `${(trackProgress / duration) * 100}%`
    : "0%";
  const trackStyling = `
    -webkit-gradient(linear, 0% 0%, 100% 0%, color-stop(${currentPercentage}, #AF74F5), color-stop(${currentPercentage}, #fff))
  `;

  const getTimeSecound = (seconds) => {
    if (seconds === -1 || seconds === 0 || seconds === 1) {
      return "00:00";
    }
    var hour = Math.floor(seconds / (60 * 60)); // 
    seconds %= 60 * 60; // 1

    var minute = Math.floor(seconds / 60); // 
    seconds %= 60; // 1

    return [minute, seconds]
      .map((num) => (num < 10 ? "0" + num : num))
      .join(":")
      .substring(0, 5);
  };

  const startTimer = () => {
    // Clear any timers already running
    clearInterval(intervalRef.current);

    intervalRef.current = setInterval(() => {
      if (audioRef.current.ended) {
        toNextTrack();
      } else {
        const past = audioRef.current.duration - audioRef.current.currentTime;
        setText(getTimeSecound(past));
        setTrackProgress(audioRef.current.currentTime);
      }
    }, [1000]);
  };

  const onScrub = (value) => {
    // Clear any timers already running
    clearInterval(intervalRef.current);
    audioRef.current.currentTime = value;
    setTrackProgress(audioRef.current.currentTime);
  };

  const onScrubEnd = () => {
    // If not already playing, start
    if (!isPlaying) {
      setIsPlaying(true);
    }
    startTimer();
  };

  const toPrevTrack = () => {
    if (trackIndex - 1 < 0) {
      setTrackIndex(audioList.length - 1);
    } else {
      setTrackIndex(trackIndex - 1);
    }
  };

  const toNextTrack = () => {
    if (trackIndex < audioList.length - 1) {
      setTrackIndex(trackIndex + 1);
    } else {
      setTrackIndex(0);
    }
  };

  useEffect(() => {
    // console.log("isPlaying", isPlaying);
    if (isPlaying) {
      audioRef.current.play();
      startTimer();
    } else {
      // clearInterval(intervalRef.current);
      audioRef.current.pause();
    }
  }, [isPlaying]);

  useEffect(() => {
    // Pause and clean up on unmount
    return () => {
      audioRef.current.pause();
      clearInterval(intervalRef.current);
      isReady.current = false;
      setIsPlaying(false);
    };
  }, []);

  //   console.log("playIndex init - ", playIndex);

  useEffect(() => {
    audioRef.current.pause();
    current = audioList[trackIndex];
    audioRef.current = new Audio(current.link);
    setTrackProgress(audioRef.current.currentTime);

    console.log("isReady.current", isReady.current);
    if (isReady.current) {
      audioRef.current.play();
      setIsPlaying(true);
      startTimer();
    } else {
      // Set the isReady ref as true for the next pass
      isReady.current = true;
    }
  }, [trackIndex]);

  const viewport = window.innerWidth;
  return (
    <div className="custom-layout" style={{ ...props.style }}>
      {/*  */}
      <div className="custom-audio">
        <div className="custom-audio-dash layout-abs">
          <div className="fx-box fx-jc-sb">
            <div className="layout-ml8 fx-box">
              <img
                mode="cover"
                className="custom-audio-cover"
                src={current.img}
              />
              <div className="custom-audio-info">
                <div className="custom-audio-info-title">{current.title}</div>
                <div className="custom-audio-info-desc">{current.desc}</div>
              </div>
            </div>
            <AudioControls
              isPlaying={isPlaying}
              onPrevClick={toPrevTrack}
              onNextClick={toNextTrack}
              onPlayPauseClick={setIsPlaying}
              trackIndex={trackIndex}
              tracksLength={audioList.length}
            />
          </div>

          <div className="costom-audio-progress fx-box fx-jc-sb fx-ai-c">
            <div className="progress-slider">
              {/* <Slider
                sliderWidth={(263 / 1920) * viewport}
                maxNumber={duration}
                current={timer}
              /> */}
              <input
                type="range"
                value={trackProgress}
                step="1"
                min="0"
                max={duration ? duration : `${duration}`}
                onChange={(e) => onScrub(e.target.value)}
                onMouseUp={onScrubEnd}
                onKeyUp={onScrubEnd}
                className="slider-input"
                style={{ background: trackStyling }}
              />
            </div>
            <div className="progress-timer layout-mr18">-{text}</div>
          </div>
        </div>
        {/* <audio
          ref={(c) => {
            setAudioRef(c);
          }}
          src={current.link}
          controls="controls"
          autoPlay
          className="custom-hide-audio full-r-w full-r-h layout-abs"
        ></audio> */}
      </div>
    </div>
  );
};

export const AudioPlayer = AudioControl;
