import { SMButtonCheckedImg } from "src/assets";
import { useCallback, useState, useEffect } from "react";
import { ConnectButton } from "@rainbow-me/rainbowkit";
import {
  socialMediaAuthorize,
  getSignMessage,
  bindSignature,
  socialMediaAuthorizeInfo,
} from "src/api";
import {
  BIND_DC_QUEST,
  BIND_WEB3_QUEST,
  TWITTER_ADD_USERNAME,
  RT_TWITTER_QUEST,
  ON_CHAIN,
  FOLLOW_TWITTER_QUEST,
} from "src/pages/task/questTypes";
import { verifyTask, removeBind } from "src/api";
import { OnChainCheckIn } from "src/components/on-chain-check-in";
import { RetweetDialog } from "src/components/retweet";
import { TweetNameDlg } from "src/components/twittername";
import { FollowTaskDialog } from "src/components/task/follow-task-dialog";
import { ChangPointModal } from "src/components/task/chang-point";
import { toast } from "react-toastify";
import { useSignMessage } from "wagmi";
import CopyIcon from "src/assets/images/copy.svg";
import { CopyToClipboard } from "react-copy-to-clipboard";

export const NewbieTask = ({ subTask }) => {
  const [dialogType, setDialogType] = useState(false);
  const [finishStatus, setFinishStatus] = useState(subTask.finishStatus);
  const [timerID, setTimerID] = useState(null);
  const [counter, setCounter] = useState(0);
  const [pointText, setPointText] = useState("");
  const [showPointText, setShowPointText] = useState(false);
  const { signMessage } = useSignMessage();

  useEffect(() => {
    if (counter > 0) {
      const timer = setTimeout(() => {
        setCounter(counter - 1);
      }, 1000);
      setTimerID(timer);
    }
    return () => {
      setTimerID(null);
    };
  }, [counter]);

  useEffect(() => {
    return () => {
      if (timerID) {
        clearTimeout(timerID);
      }
    };
  }, [timerID]);
  const renderBindWeb3Quest = (item, finishStatus) => {
    return finishStatus === 0 ? (
      // <ConnectButton/>
      <ConnectButton.Custom>
        {({
          account,
          chain,
          openAccountModal,
          openChainModal,
          openConnectModal,
          authenticationStatus,
          mounted,
        }) => {
          // Note: If your app doesn't use authentication, you
          // can remove all 'authenticationStatus' checks
          const ready = mounted && authenticationStatus !== "loading";
          const connected =
            ready &&
            account &&
            chain &&
            (!authenticationStatus || authenticationStatus === "authenticated");

          if (chain?.unsupported) {
            return (
              <div
                className="quest-list-btn quest-btn-3 layout-ml17"
                onClick={() => {
                  openChainModal();
                }}
              >
                Chain
              </div>
            );
          }
          if (!connected) {
            return (
              <div
                className="quest-list-btn quest-btn-3 layout-ml17"
                onClick={() => {
                  openConnectModal();
                }}
              >
                Connect
              </div>
            );
          }
          return (
            <div
              className="quest-list-btn quest-btn-3 layout-ml17"
              onClick={async () => {
                getSignMessage(chain.id, account.address).then(async (res) => {
                  if (res.code === 200) {
                    await signMessage(
                      { message: res.data.message },
                      {
                        onSuccess: async (signature) => {
                          const callBackRes = await bindSignature(
                            res.data.keyId,
                            signature
                          );
                          if (callBackRes.code === 200) {
                            toast.success("success");
                            setFinishStatus(1);
                          } else {
                            toast.error(callBackRes.msg);
                          }
                        },
                      }
                    );
                  }
                });
              }}
            >
              Bind
            </div>
          );
        }}
      </ConnectButton.Custom>
    ) : (
      <>
        <div
          onClick={() => {
            removeBind("Metamask").then((res) => {
              if (res.code === 200) {
                setShowPointText(true);
                setPointText(`- ${subTask.reward}`);
                toast.success("success");
                setFinishStatus(0);
              } else {
                toast.error(res.msg);
              }
            });
          }}
          className="quest-list-btn quest-btn-2 layout-ml17"
        >
          Unbind
        </div>
        <div className="quest-list-checkbox fx-box fx-ai-c layout-ml24">
          <img src={SMButtonCheckedImg} alt="" />
        </div>
      </>
    );
  };

  const renderBindDCQuest = (item, finishStatus) => {
    if (finishStatus === 1) {
      return (
        <>
          <div
            onClick={() => {
              removeBind("discord").then((res) => {
                if (res.code === 200) {
                  setShowPointText(true);
                  setPointText(`- ${subTask.reward}`);
                  toast.success("success");
                  setFinishStatus(0);
                } else {
                  toast.error(res.msg);
                }
              });
            }}
            className="quest-list-btn quest-btn-2 layout-ml17"
          >
            Unbind
          </div>
          <div className="quest-list-checkbox fx-box fx-ai-c layout-ml24">
            <img src={SMButtonCheckedImg} alt="" />
          </div>
        </>
      );
    } else if (finishStatus === 0) {
      return (
        <div
          className=""
          onClick={() => {
            // socialMediaAuthorizeInfo().then((authorizeInfo) => {
            //   if (authorizeInfo.code === 200) {
            //     if (authorizeInfo.data["twitter"]) {
            //       window.open(item.url);
            //       setDialogType(item.taskType);
            //     } else {
            //       socialMediaAuthorize("twitter").then((res) => {
            //         if (res.code === 200 && res.data) {
            //           window.open(res.data, "_blank");
            //         }
            //       });
            //     }
            //   }
            // });
            socialMediaAuthorize().then((res) => {
              if (res.code === 200) {
                window.open(res.data);
              }
            });
          }}
        >
          <div className="quest-list-btn quest-btn-3 layout-ml17">Bind</div>
        </div>
      );
    }
  };

  const renderQuest = (item, finishStatus) => {
    if (finishStatus === 0)
      return (
        <div
          className="quest-list-btn quest-btn-3 layout-ml17"
          onClick={() => {
            socialMediaAuthorizeInfo().then((authorizeInfo) => {
              if (authorizeInfo.code === 200) {
                if (authorizeInfo.data["twitter"]) {
                  window.open(item.url);
                  setDialogType(item.taskType);
                } else {
                  socialMediaAuthorize("twitter").then((res) => {
                    if (res.code === 200 && res.data) {
                      window.open(res.data, "_blank");
                    }
                  });
                }
              }
            });
          }}
        >
          {item.taskType === TWITTER_ADD_USERNAME
            ? "GO"
            : item.taskType === FOLLOW_TWITTER_QUEST
            ? "Follow"
            : "Bind"}
        </div>
      );
    else if (finishStatus === 1)
      return (
        <div className="quest-list-btn quest-btn-2 layout-ml17">Completed</div>
      );
  };
  const renderQuestItem = useCallback(
    (item) => {
      // item.taskType=BIND_WEB3_QUEST;
      if (item.taskType === BIND_WEB3_QUEST) {
        return renderBindWeb3Quest(item, finishStatus);
      }
      if (item.taskType === BIND_DC_QUEST) {
        return renderBindDCQuest(item, finishStatus);
      } else return renderQuest(item, finishStatus);
    },
    [finishStatus]
  );
  const verify = () => {
    verifyTask(subTask.activityId, subTask.id).then((res) => {
      if (res.code === 200) {
        toast.success(res.msg);
        setShowPointText(true);
        setPointText(`+ ${subTask.reward}`);
        setFinishStatus(1);
      } else {
        // toast.error(res.msg);
        setCounter(60);
      }
    });
  };

  return (
    <>
      <div className="quest-list-item fx-box fx-jc-sb fx-ai-c">
        <div className="quest-list-left">
          {subTask.title}
          {subTask.taskType === TWITTER_ADD_USERNAME && (
            <CopyToClipboard text={" $ARIA"}>
              <img
                alt=""
                className="copy-icon"
                src={CopyIcon}
                width={20}
                height={20}
              ></img>
            </CopyToClipboard>
          )}
        </div>
        <div className="fx-box fx-ai-c quest-list-right">
          <div className="quest-list-btn quest-btn-1 fx-box fx-ai-c fx-jc-sb">
            <img
              className="diamont-icon"
              src={"/img/diamont-icon.png" || subTask.rewardIcon}
              alt=""
            />
            <span className="diamont-text">{subTask.reward}</span>
          </div>

          {renderQuestItem(subTask)}
        </div>
      </div>

      {/* <FollowTaskDialog visible={showFTDlg} quest={quest} onClose={()=>{setShowFTDlg(false)}}/> */}
      <FollowTaskDialog
        visible={dialogType === FOLLOW_TWITTER_QUEST}
        quest={subTask}
        counter={counter}
        onClose={() => {
          setDialogType(0);
        }}
        validate={() => {
          verify();
        }}
      />
      <OnChainCheckIn
        visible={dialogType === ON_CHAIN}
        quest={subTask}
        counter={counter}
        onClose={() => {
          setDialogType(0);
        }}
      />
      <RetweetDialog
        visible={dialogType === RT_TWITTER_QUEST}
        quest={subTask}
        counter={counter}
        validate={() => {
          verify();
        }}
        onClose={() => {
          setDialogType(0);
        }}
      />
      <TweetNameDlg
        visible={dialogType === TWITTER_ADD_USERNAME}
        quest={subTask}
        counter={counter}
        validate={() => {
          verify();
        }}
        onClose={() => {
          setDialogType(0);
        }}
      />
      <ChangPointModal
        text={pointText}
        visible={showPointText}
        onClose={() => {
          setPointText("");
          setShowPointText(false);
        }}
      ></ChangPointModal>
    </>
  );
};
