import "./players.css";
import {
    PlayerDefault,
    PlayerPlay,
    PlayerPaused,
    PlayerNext,
    PlayerPrev,
    PlayerNextActive,
    PlayerPrevActive,
    PlayerVolume,
    PlayerVolumeActive,
} from "../../assets/index";

const AudioControls = ({
    isPlaying,
    onPlayPauseClick,
    onPrevClick,
    onNextClick,
    trackIndex,
    tracksLength
}) => (
    <div className="custom-audio-controls fx-box fx-ai-c layout-mr18">
        <div className="custom-audio-controls-last layout-mr10">
            <img
                className="basic-icon20"
                alt=""
                src={trackIndex === 0 ? PlayerPrev : PlayerPrevActive}
                onClick={onPrevClick}
            />
        </div>
        <div className="custom-audio-controls-play layout-mr10">
            <img
                onClick={() => {
                    isPlaying
                        ? onPlayPauseClick(false)
                        : onPlayPauseClick(true)
                }}
                className="basic-icon20"
                alt=""
                src={!isPlaying ? PlayerPlay : PlayerPaused}
            />
        </div>
        <div className="custom-audio-controls-next layout-mr18">
            <img
                className="basic-icon20"
                alt=""
                src={
                    trackIndex === tracksLength - 1
                        ? PlayerNext
                        : PlayerNextActive
                }
                onClick={onNextClick}
            />
        </div>
        <div className="custom-audio-controls-volume">
            <img
                className="basic-icon20"
                alt=""
                src={isPlaying ? PlayerVolumeActive : PlayerVolume}
            />
        </div>
    </div>
)

export default AudioControls;