import { useEffect, useMemo, useRef, useState } from "react";
import "./gameplay.css";
import {
  HomeVideo,
  leftArrowIcon,
  playIcon,
  youtubeIcon1,
  GamePlayVideo,
} from "src/assets";
import { useNavigate } from "react-router-dom";

export const GameplayPage = () => {
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  const [windowHeight, setWindowHeight] = useState(window.innerHeight);

  const [showPlayButton, setShowPlayButton] = useState(true);
  const videoRef = useRef();
  const navigate = useNavigate();

  const handlePlay = () => {
    videoRef?.current?.play();
  };
  useEffect(() => {
    const videoCheckTimer = setInterval(() => {
      setShowPlayButton(videoRef?.current?.paused);
    }, 500);
    return () => {
      clearInterval(videoCheckTimer);
    };
  }, []);

  const handleResize = () => {
    setWindowWidth(window.innerWidth);
    setWindowHeight(window.innerHeight);
  };

  const { videoWidth, videoHeight, videoLeft } = useMemo(() => {
    if (windowHeight * (16 / 9) > windowWidth) {
      //<16/9
      return {
        videoHeight: windowHeight,
        videoWidth: windowHeight * (16 / 9),
        videoLeft: `calc(50% - ${Math.trunc(windowHeight * (8 / 9))}px)`,
      };
    } else {
      //>16/9
      return {
        videoHeight: (windowWidth * 9) / 16,
        videoWidth: windowWidth,
        videoLeft: 0,
      };
    }
  }, [windowWidth, windowHeight]);
  console.log("[debug]video size:", videoWidth, videoHeight, videoLeft);
  useEffect(() => {
    window.addEventListener("resize", handleResize);

    // 
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  return (
    <div className="full-r-w gameplay">
      <video
        className="play-video"
        style={{
          height: videoHeight,
          width: videoWidth,
          marginLeft: videoLeft,
        }}
        ref={videoRef}
        muted={false}
        controls={!showPlayButton}
        autoPlay={false}
        src={GamePlayVideo}
      ></video>

      <div className="game-mask">
        {showPlayButton ? (
          <div className="play-button" onClick={handlePlay}>
            <img src={playIcon} />
          </div>
        ) : null}
        <div className="play-bottom">
          <div style={{ paddingLeft: 30 }}>
            <div
              className="play-return"
              onClick={() => {
                navigate(-1);
              }}
            >
              <img src={leftArrowIcon} />
            </div>
          </div>
          <div className="play-video-title">
            <div className="h1">Video Title Content</div>
            <div className="h2">Here's a video synopsis of the content</div>
          </div>
          <div>
            <a
              className="game-youtube-link"
              href="https://youtu.be/dOxcqfghtks"
              target="_blank"
            >
              <div className="play-youtube">
                <div>Go to YouTube to watch</div>

                <img className="player-youtube-img" src={youtubeIcon1} />
              </div>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};
