const TeamMemberTwitterBadge = (props) => {
  return (
    <div className="twitter">
      <div className="twitter-logo">
        <img src={XLogo} />
      </div>
      <div>
        <a href={props.twitter} target="_blank">
          {" "}
          @{props.title}
        </a>
      </div>
    </div>
  );
};

const TeamMember = (props) => {
  const { member } = props;
  return (
    <div className="team-member">
      <img src={props.img} className="avatar"></img>
      <span className="member-title">{props.title}</span>
      <div className="member-detail">
        <div className="fx-box fx-ai-c team-mb">
          <img className="avatar" src={props.img} />
          <div className="fx-box fx-fd-c" style={{ marginLeft: 11 }}>
            <div className="member-name">{props.title}</div>
            <div className="member-job">{member.job}</div>
          </div>
        </div>
        {member.introduction?.map((intro, index) => {
          return <div className="team-member-intro">{intro}</div>;
        })}
        {member?.twitter ? (
          <div style={{ width: "auto", display: "flex" }}>
            <TeamMemberTwitterBadge
              title={member.twitter}
              twitter={member.twitterLink}
            />
            <div style={{ flex: 1 }}></div>
          </div>
        ) : null}
      </div>
    </div>
  );
};

