// import { HashRouter as Router,Routes, Route} from 'react-router-dom'
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";

import { MainLayout } from "../layout/main";
import { Home } from "../pages/home";
import { Task } from "../pages/task";
import { Team } from "src/pages/team";
import { CallbackScren } from "src/pages/callback";
import { CallbackDiscord } from "src/pages/callback/CallbackDiscord";
import { Welcome } from "src/pages/welcome";
import { RoadMap } from "src/pages/roadmap";
import { Terms } from "src/pages/terms";
import { Privacy } from "src/pages/terms/privacy";
import { GameplayPage } from "src/pages/gameplay";

export const Routers = (props) => {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<Welcome />}></Route>
        <Route path="/app" element={<MainLayout />}>
          <Route path="" element={<Home />} />
          <Route path="quest" element={<Task />} />
          <Route path="team" element={<Team />} />
          <Route path="roadmap" element={<RoadMap />} />
          <Route path="terms" element={<Terms />} />
          <Route path="privacy" element={<Privacy />} />
          <Route path="gameplay" element={<GameplayPage />} />
        </Route>
        <Route path="/callback" element={<CallbackScren />} />
        <Route path="/callback/:type" element={<CallbackDiscord />} />
      </Routes>
    </Router>
  );
};
