import {
  verifyTask,
  socialMediaAuthorizeInfo,
  socialMediaAuthorize,
} from "src/api";
import { ChangPointModal } from "src/components/task/chang-point";
import { useEffect, useState } from "react";
import { toast } from "react-toastify";
export const QuestListItem = ({ subTask }) => {
  const [timerID, setTimerID] = useState(null);
  const [counter, setCounter] = useState(0);
  const [pointText, setPointText] = useState("");
  const [showPointText, setShowPointText] = useState(false);
  const [finishStatus, setFinishStatus] = useState(subTask.finishStatus);
  useEffect(() => {
    if (counter > 0) {
      const timer = setTimeout(() => {
        setCounter(counter - 1);
      }, 1000);
      setTimerID(timer);
    }
    return () => {
      setTimerID(null);
    };
  }, [counter]);

  useEffect(() => {
    return () => {
      if (timerID) {
        clearTimeout(timerID);
      }
    };
  }, [timerID]);
  return (
    <>
      <div className="quest-list-item quest-2-item fx-box fx-jc-sb fx-ai-c">
        <div
          className="quest-2-left fx-box fx-fd-c fx-jc-c"
          onClick={() => {
            window.open(subTask.url);
          }}
        >
          <div>{subTask.title}</div>
          <div>{subTask.description}</div>
        </div>

        <div className="fx-box fx-ai-c quest-list-right">
          <div className="quest-list-btn quest-btn-1 fx-box fx-ai-c fx-jc-sb">
            <img
              alt=""
              className="diamont-icon"
              src={"/img/diamont-icon.png" || subTask.rewardIcon}
            />
            <span className="diamont-text">{subTask.reward}</span>
          </div>
          {finishStatus === 0 ? (
            <>
              <div
                className="quest-list-btn quest-btn-4 layout-ml24"
                onClick={() => {
                  if (counter === 0) {
                    socialMediaAuthorizeInfo().then((authorizeInfo) => {
                      if (authorizeInfo.code === 200) {
                        if (authorizeInfo.data["twitter"]) {
                          verifyTask(subTask.activityId, subTask.id).then(
                            (res) => {
                              if (res.code === 200) {
                                setShowPointText(true);
                                setPointText(`+ ${subTask.reward}`);
                                toast.success(res.msg);
                                setFinishStatus(1);
                              } else {
                                // toast.error(res.msg);
                                setCounter(60);
                              }
                            }
                          );
                        } else {
                          socialMediaAuthorize("twitter").then((res) => {
                            if (res.code === 200 && res.data) {
                              window.open(res.data, "_blank");
                            }
                          });
                        }
                      }
                    });
                  }
                }}
              >
                Verify {counter > 0 ? `( ${counter} s)` : ""}
              </div>
            </>
          ) : (
            <div className="quest-list-btn quest-btn-2 layout-ml24">
              Completed
            </div>
          )}
        </div>
      </div>
      <ChangPointModal
        text={pointText}
        visible={showPointText}
        onClose={() => {
          setPointText("");
          setShowPointText(false);
        }}
      ></ChangPointModal>
    </>
  );
};
