import { useState } from "react";

import { CopyToClipboard } from "react-copy-to-clipboard";


const CopyImg = "/img/copy-icon.svg";
const CopiedImg = "/img/copied-icon.svg";

export const CopyIcon = ({value})=>{
    const [copyImg,setCopyImg] =  useState(CopyImg);
    return (
        <CopyToClipboard text = {value} onCopy={()=>{
            setCopyImg(CopiedImg);
            setTimeout(()=>{
                setCopyImg(CopyImg);
            },2000)
        }} >
        <img className="basic-icon20" src={copyImg} />
        </CopyToClipboard>
    )
}

