import { closeIcon } from "src/assets"
import { Dialog } from "../dialog"
import { setInvitationCode } from "src/api";
import { useState } from "react";
import './welcome.css';
import ConfettiCanvas from "../Confetti";

export const BindReferralDlg = ({visible,onClose})=>{
    const [code,setCode] = useState('');
    const [invited,setInvited] = useState(false);
    return (
        <Dialog visible={visible} mask={false} onClose={onClose}>
            <div className="welcome-referral">
                <div className="fx-box welcome-header">
                        <div className="welcome-caption">
                            Welcome to Aria Game!
                        </div>
                        <div style={{marginLeft:'auto',cursor:'pointer'}} onClick={()=>{
                            console.log('.......');
                            onClose();
                        }}>
                            <img src={closeIcon}/>
                        </div>
                </div>
                <div className="fx-box fx-fd-c fx-ai-c">
                        <div className="welcome-title">
                            You Got
                        </div>
                        <div className="welcome-points">
                            +400 PTS
                        </div>
                        <div className="welcome-desc">
                        Input a referrals code if you' ve got one for extra PTS
                        </div>
                        <div className="welcome-input">
                            <input value={code} onChange={(e)=>{
                                setCode(e.target.value)
                            }}>
                            </input>
                            <div className="go-btn" onClick={()=>{
                                setInvitationCode(code).then(ret=>{
                                    onClose();
                                    if(ret.code==200){
                                        setInvited(true);
                                    }
                                }).catch(e=>{
                                    console.log(e);
                                }).finally(()=>{
                                    setInvited(true);
                                    setTimeout(()=>{
                                        setInvited(false)
                                    },5000)
                                });
                            }}>
                                Go
                            </div>
                        </div>
                </div>
            </div>
            <ConfettiCanvas fireConfetti={invited}/>
        </Dialog>
    )
}