import { Provider } from 'react-redux';

import './App.css';
import store from './redux';
import { Routers } from './routers';
import { getUserInfo, updateAuth } from './api';
import { ACCESS_TOKEN_ITEM_NAME } from './constants';
import { useEffect } from 'react';

function App() {
    
    useEffect(()=>{
        const accessToken = localStorage.getItem(ACCESS_TOKEN_ITEM_NAME);
        updateAuth(accessToken);
        getUserInfo().then(ret=>{
            console.log('[debug]get user info:',ret);
            if(ret?.code==200){
                store.dispatch({type:'LOGIN_DONE',payload:ret.data});
            }
        }).catch(err=>{
            console.error()
        });
    },[])
  return (
    <Provider store = {store}>
        <Routers></Routers>
    </Provider>
  );
}

export default App;
