import './dialog.css';

export const Dialog = ({ visible, children, onClose, style, mask }) => {
    return (
        <div>
            <div className={"dialog " +(mask===false?'nomask':'')+ (visible ? ' show' : ' hide')} onClick={onClose}>
                <div className="full-r-w full-r-h fx-box fx-jc-c fx-ai-c" >
                    <div onClick={(e) => {
                        e.stopPropagation();
                    }} style={style}>
                        {children}
                    </div>
                </div>
            </div>
        </div>
    )
}