import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import '@rainbow-me/rainbowkit/styles.css';
import {
  getDefaultConfig,
  RainbowKitProvider,
} from '@rainbow-me/rainbowkit';
import { WagmiProvider, createConfig, http } from 'wagmi';
import {
  mainnet,
  polygon,
  optimism,
  arbitrum,
  base,
  zora,
  bscTestnet,
} from 'wagmi/chains';
import {
  QueryClientProvider,
  QueryClient,
} from "@tanstack/react-query";
import 'react-toastify/dist/ReactToastify.css';
import { ToastContainer } from 'react-toastify';

import { connectorsForWallets } from '@rainbow-me/rainbowkit';
import {
  rainbowWallet,
  walletConnectWallet,
  okxWallet,
  coinbaseWallet,
  metaMaskWallet
} from '@rainbow-me/rainbowkit/wallets';

const connectors = connectorsForWallets(
  [
    {
      groupName: 'Recommended',
      wallets: [rainbowWallet, walletConnectWallet, coinbaseWallet, okxWallet, metaMaskWallet],
    },
  ],
  {
    appName: 'Play Aria Game',
    projectId: 'd8beff6c54a1e74df1db8a2304ba767d',
  }
);

const queryClient = new QueryClient();

// const config = getDefaultConfig({
//     appName: 'Play Aria Game',
//     projectId: 'd8beff6c54a1e74df1db8a2304ba767d',
//     chains: [mainnet, polygon, optimism, arbitrum, base, zora],
//     ssr: false, // If your dApp uses server side rendering (SSR)
// });
const config = createConfig({
  connectors,
  chains: [mainnet, polygon, optimism, arbitrum, base, zora, bscTestnet],
  transports: {
    [bscTestnet.id]: http('https://data-seed-prebsc-1-s1.binance.org:8545'),
  },
});

const logLogo = `






`;

console.log('%c %s', 'color: #af74f5', logLogo);
console.log('%c Play Aria Game v0.02', 'color: #af74f5');


const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <WagmiProvider config={config}>
      <QueryClientProvider client={queryClient}>
        <RainbowKitProvider>
          <ToastContainer></ToastContainer>
          <App style={{ minWidth: '1024px' }} />
        </RainbowKitProvider>
      </QueryClientProvider>
    </WagmiProvider>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
