import { useState } from "react";
import { OnChainCheckIn } from "src/components/on-chain-check-in";

export const OnChainItem = ({ subTask }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [finishStatus, setFinishStatus] = useState(subTask.finishStatus);

  return (
    <>
      <OnChainCheckIn
        visible={isOpen}
        quest={subTask}
        onClose={() => {
          setIsOpen(false);
        }}
      />
      <div
        key={`${subTask.id}-${subTask.title}`}
        className="quest-list-item quest-2-h fx-box fx-jc-sb fx-ai-c"
      >
        <div className="quest-list-left">{subTask.title}</div>
        <div
          className="quest-list-right"
          onClick={(type) => {
            if (type === "success") {
              setFinishStatus(1)
            }
            setIsOpen(true);
          }}
        >
          {finishStatus === 1 ? (
            <div className="quest-list-btn quest-btn-2 layout-ml17">
              Completed
            </div>
          ) : (
            <div className="quest-list-btn quest-btn-3 layout-ml17">Click</div>
          )}
        </div>
      </div>
    </>
  );
};
