import { closeIcon } from "src/assets";
import { Dialog } from "../dialog";
import { setInvitationCode } from "src/api";
import { useEffect, useState } from "react";
import "./chang-point.css";
import ConfettiCanvas from "../Confetti";
import { connect } from "react-redux";
import { getUserInfo } from "src/api";

export const ChangPointModal = connect(
  (state) => ({
    profile: state.profile,
  }),
  (dispatch) => ({ dispatch })
)((props) => {
  useEffect(() => {
    if (props.visible) {
      getUserInfo()
      .then((ret) => {
        if (ret?.code == 200) {
          props.dispatch({ type: "LOGIN_DONE", payload: ret.data });
        }
      })
      .catch((err) => {
        console.error();
      });
    }
  }, [props.visible]);
  return (
    <Dialog visible={props.visible} mask={false} onClose={props.onClose}>
      <div className="welcome-referral">
        <div className="fx-box welcome-header">
          <div className="welcome-caption">Aria Mission</div>
          <div
            style={{ marginLeft: "auto", cursor: "pointer" }}
            onClick={() => {
              props.onClose();
            }}
          >
            <img src={closeIcon} />
          </div>
        </div>
        <div className="fx-box fx-fd-c fx-ai-c">
          <div className="welcome-title">You Got</div>
          <div className="welcome-points">{props.text}</div>
        </div>
        <div className="referrals-code">
          <div className="label">Referrals code</div>
          <div className="value">{props.profile.totalReferrals}</div>
        </div>
      </div>
    </Dialog>
  );
});
