import { Box, CircularProgress } from "@mui/material";
import { useEffect } from "react";
import { connectXCallback, updateAuth } from "src/api";

export const CallbackScren = (props) => {
  useEffect(() => {
    let href = window.location.href;
    // const start = href.indexOf('/?');
    // const end = href.indexOf('#/');

    href = href.replace("https://playariagame.com/callback?", "");
    const qs = href?.split("&");
    let oauth_token = "";
    let oauth_verifier = "";
    qs?.forEach((q) => {
      if (q.startsWith("oauth_token")) {
        oauth_token = q.replace("oauth_token=", "");
      } else if (q.startsWith("oauth_verifier")) {
        oauth_verifier = q.replace("oauth_verifier=", "");
      }
    });
    if (oauth_token && oauth_verifier) {
      connectXCallback(oauth_token, oauth_verifier)
        .then((ret) => {
          ret.data?.token && localStorage.setItem("jwt_token", ret.data.token);
          localStorage.setItem("firstLogin", ret.data.firstLogin)
          updateAuth(ret.data.token);
          window.location.href = "/#/app/quest";
          // navigate('/app/quest');
          // window.location.reload();
        })
        .catch((err) => {
          console.error(err);
        });
    } else {
      console.log(".......qs is right?", qs);
    }
  }, []);
  return (
    <Box
      sx={{
        height: "100%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <Box
        sx={{
          mt: 12,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <Box>
          <CircularProgress />
        </Box>
        <Box>...</Box>
      </Box>
    </Box>
  );
};
