import { createStore } from "redux";
import { combineReducers } from "redux";

const loginState = {
    profile:null,
    error:null,
    logining:false,
}

const loginReducer=function(state = loginState,action){
    // console.log('[REDUX]',action);
    switch(action.type){
        case 'LOGIN_START':
            return Object.assign({},state,{logining:true});
        case 'LOGIN_DONE':
            return Object.assign({},state,{profile:action.payload});
        case 'LOGIn_ERROR':
            return Object.assign({},state,{error:action.payload});
        case 'LOGOUT':
            return Object.assign({},state,{profile:null});
        default:
            return state;
    } 
}

export default createStore(loginReducer);

